.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.big-container {
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  overflow-x: hidden;
  opacity: 0;
  display: flex;
  justify-content: center;
}

.top-shadow, .bottom-shadow{
  width: 100%;
  position: fixed;
  height: 20px;
  z-index: 50;
}

.top-shadow{
  background-image: linear-gradient(to top, rgba(255,0,0,0), rgba(30,30,30,0.6));
}

.bottom-shadow{
  bottom: 0;
  background-image: linear-gradient(to bottom, rgba(255,0,0,0), rgba(30,30,30,0.6));
}

.mid-contaier{
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* grid-template-columns: 0.8fr 1.2fr; */
  max-width: 1400px;
  width: 100vw;
}

.section-container {
  display: flex;
  flex-direction: column;
  padding: 90px 50px 0 50px;
}

.left-container {
  pointer-events: none;
  height: 100vh;
  position: fixed;
  padding-right: 0px;
  align-items: flex-start;
  justify-content: space-between;
}

.right-container {
  max-width: 836px;
  position: absolute;
  right: 0;
  padding-left: 0px;
  align-items: flex-end;
}

.intro .title {
  -webkit-transform-origin-x: 0;
  transform: scale(1.23, 1.1);
  display: flex;
  font-size: 3em;
  font-weight: bold;
  color: #a9b7c5;
  margin-bottom: 3px;
}

.intro .subtitle {
  font-size: 0.9em;
}

.intro .i-text {
  margin-top: 5px;
  white-space: pre-wrap;
  color: #a9b7c5;
  font-size: 1.75em;
  max-width: 380px;
}

.menu {
  margin-top: 50px;
  margin-bottom: 50px;
}

.option {
  pointer-events: all;
  cursor: pointer;
  display: flex;
  color: #a9b7c5;
  margin-top: 10px;
  align-items: center;
  transition: all 0.2s ease-in-out;
}

.opt-separator {
  width: 100px;
  height: 1px;
  background-color: #a9b7c5;
  margin-left: 5px;
  margin-right: 5px;
  transition: all 0.2s ease-in-out;
}

.footer-path {
  padding-bottom: 50px;
  display: flex;
}

.footer-path > .photo {
  overflow: hidden;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  border: solid 4px #ffc66d;
}

.footer-path > .photo > img {
  width: 50px;
}

.social {
  pointer-events: all;
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.s-network {
  display: flex;
  align-items: center;
  background-color: #232325;
  padding: 10px;
  color: white;
  cursor: pointer;
}

.s-network:hover {
  background-color: #111111;
}

.s-n-ico {
  text-align: center;
  font-size: 0.8em;
  margin: 10px;
}

.uncutable {
  overflow: hidden;
}

.sec-title{
  margin-bottom: 20px;
  display: none;
}

.section-space{
  margin-bottom: 30px;
}

.selected{
  color: white;
  font-size: 2em;
}

.selected .opt-separator{
  height: 2px;
  background-color: white;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #2B2B2B;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media (max-width: 1300px) {
