  }

  .section-container{
    width: 100% !important;
  }

  .left-container {
    position: relative;
  }
  
  .right-container {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
    position: relative;
  }

  .intro{
    margin-bottom: 40px;
  }

  .intro .i-text-container {
    display: flex;
    flex-wrap: wrap;
  }

  .menu {
    display: none;
  }
}

@media (max-width: 600px) {

  
