    margin-top: 15px;
    font-size: 3em;
    margin-bottom: 10px;
  }

  .intro{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
  }

  .intro .i-text-container {
    flex-direction: column;
    align-items: center;
  }

  .intro .i-text {
    font-size: 1.2em;
  }

  .right-container {
    padding-top: 0 ;
  }

  .top-path{
    width: 100%;
  }

  .s-network {
    padding: 2px;
  }

  .footer-path{
    width: 100%;
  }

  .social{
    width: 100%;
    margin: 0;
  }

  .s-network {
    width: 100%;
  }

  .footer-path .photo{
    display: none;
  }

  .title-container{
    /* margin-left: -45px; */
  }
}
