/* -----------------Spinner-------------------- */

.spinner {
  margin: 100px auto;
  width: 40px;
  height: 40px;
  position: relative;
}

.cube1, .cube2 {
  background-color: white;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0;
  left: 0;
  
  -webkit-animation: sk-cubemove 1.8s infinite ease-in-out;
  animation: sk-cubemove 1.8s infinite ease-in-out;
}

.cube2 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

@-webkit-keyframes sk-cubemove {
  25% { -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5) }
  50% { -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg) }
  75% { -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5) }
  100% { -webkit-transform: rotate(-360deg) }
}

@keyframes sk-cubemove {
  25% { 
    transform: translateX(42px) rotate(-90deg) scale(0.5);
    -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
  } 50% { 
    transform: translateX(42px) translateY(42px) rotate(-179deg);
    -webkit-transform: translateX(42px) translateY(42px) rotate(-179deg);
  } 50.1% { 
    transform: translateX(42px) translateY(42px) rotate(-180deg);
    -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
  } 75% { 
    transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
    -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
  } 100% { 
    transform: rotate(-360deg);
    -webkit-transform: rotate(-360deg);
  }
}
.shp1 {
  fill: transparent;
  stroke-dasharray: 140 540;
  stroke-dashoffset: -300;
  stroke-width: 100px;
  stroke: #19f6e8;
}

.container {
  z-index: 70;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  transition: all 0.2s ease-in-out;
  flex-direction: column;
}

@-webkit-keyframes draw {
  0% {
    stroke-dasharray: 140 540;
    stroke-dashoffset: -400;
  }
  100% {
    stroke-dasharray: 760;
    stroke-dashoffset: 0;
  }
}

@keyframes draw {
  0% {
    stroke-dasharray: 140 540;
    stroke-dashoffset: -400;
  }
  100% {
    stroke-dasharray: 760;
    stroke-dashoffset: 0;
  }
}

@-webkit-keyframes draw2 {
  0% {
    opacity: 0;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes draw2 {
  0% {
    opacity: 0;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes draw3 {
  0% {
    stroke: #000000;
  }
  50% {
    stroke: #000000;
  }
  100% {
    stroke: #FFC66D;
  }
}

@keyframes draw3 {
  0% {
    stroke: #000000;
  }
  50% {
    stroke: #000000;
  }
  100% {
    stroke: #FFC66D;
  }
}

@-webkit-keyframes draw4 {
  0% {
    fill: transparent;
  }
  50% {
    fill: transparent;
  }
  100% {
    fill:#FFC66D;
  }
}

@keyframes draw4 {
  0% {
    fill: transparent;
  }
  50% {
    fill: transparent;
  }
  100% {
    fill:#FFC66D;
  }
}

@-webkit-keyframes increase {
  0% {
    transform: scale(1,1);
  }
  30% {
    transform: scale(1,1);
  }
  100% {
    transform: scale(1.1,1.1);
  }
}

@keyframes increase {
  0% {
    transform: scale(1,1);
  }
  30% {
    transform: scale(1,1);
  }
  100% {
    transform: scale(1.1,1.1);
  }
}

.loading-text {
  -webkit-animation: 6s draw2 linear forwards;
  animation: 6s draw2 linear forwards;
}

.txt {
  -webkit-animation: 2s draw linear forwards, 4s draw3 linear forwards,
    4s draw4 linear forwards;
  animation: 2s draw linear forwards, 4s draw3 linear forwards,
    4s draw4 linear forwards;
}

.shp1 {
  -webkit-animation: 2s draw linear forwards, 4s draw3 linear forwards;
  animation: 2s draw linear forwards, 4s draw3 linear forwards;
}

.loader-svg{
  -webkit-animation: 12s increase linear forwards;
  animation: 12s increase linear forwards;
}

.loaded {
  opacity: 0;
  pointer-events: none;
}

